import React from "react";

const SecondLevelContent = ({ subjects, setHistory }) => {
  //console.log(subjects)
  console.log("contents in slc", subjects.content);
  return (
    <button
      className="second-level-style"
      onClick={() =>
        setHistory({
          path: "video",
          id: subjects.content[0].id,
          contents: subjects.content
        })
      }
    >
      {subjects.Subject}
    </button>
  );
};

export default SecondLevelContent;
