import { connect } from "react-redux";
import { addCourse, editCourse } from "../../creators/singleCourseCreator";
import { setActiveUser } from "../../creators/activeUserCreator";
import { editUser } from "../../creators/userCreator";
import { updateActiveCourse, updateScore } from "../../creators/activeCourseCreator";
import Quiz from "./Quiz";
import { setHistory } from "../../creators/historyCreator";

//remove courses when connecting to api
const mapStateToProps = ({ singleCourse, activeCourse, courses, mode, history, user }) => {
    console.log("user from connect", user)
    return { singleCourse, activeCourse, courses, mode, history, user };
}

const ConnectedQuiz = connect(mapStateToProps, { addCourse, updateScore, editUser, setActiveUser, editCourse, setHistory, updateActiveCourse })(Quiz);

export default ConnectedQuiz;