import React from 'react'

const RegisterForm = (props) => {
    return (
        <div className="container-fluid register-wrapper">
            <div className="container">
                <div className="row">
                    <div className="col-md-5 offset-md-1 package">
                        <h2>
                            Here’s what is included<br />
                            in your Free Trial
                        </h2>

                        <div className="row packages">
                            <div className="col-md-12 col-6 padding-left0">
                                <span className="package-content">
                                    <span className="wrap-text">
                                        <span className="head">Classes</span><br />
                                        <span className="desc">Schedule and deliver live online classes</span>
                                    </span>

                                </span>
                            </div>
                            <div className="col-md-12 col-6 padding-left0">
                                <span className="package-content">
                                    <span className="wrap-text">
                                        <span className="head">Classes</span><br />
                                        <span className="desc">Schedule and deliver live online classes</span>
                                    </span>

                                </span>
                            </div>
                            <div className="col-md-12 col-6 padding-left0">
                                <span className="package-content">
                                    <span className="wrap-text">
                                        <span className="head">Classes</span><br />
                                        <span className="desc">Schedule and deliver live online classes</span>
                                    </span>

                                </span>
                            </div>
                            <div className="col-md-12 col-6 padding-left0">
                                <span className="package-content">
                                    <span className="wrap-text">
                                        <span className="head">Classes</span><br />
                                        <span className="desc">Schedule and deliver live online classes</span>
                                    </span>

                                </span>
                            </div>
                        </div>

                    </div>
                    <div className="col-md-4 register-details">


                        <form onSubmit={props.submitForm}>
                            <div className="form-row">
                                <div className="form-group col-6">

                                    <input {...props.firstNameField} required className="form-control" placeholder="First Name" />
                                </div>
                                <div className="form-group col-6">

                                    <input {...props.surNameField} required className="form-control" placeholder="Last Name" />
                                </div>
                            </div>
                            <div className="form-group">
                                <input {...props.emailField} required className="form-control" placeholder="Email" />
                            </div>

                            <div className="form-row">
                                <div className="form-group col-4">
                                    <select value={props.sex} onChange={(e) => props.setSex(e.target.value)} className="form-control select_">
                                        <option>Female</option>
                                        <option>Male</option>
                                    </select>
                                </div>
                                <div className="form-group col-8">
                                    <input {...props.phoneNumberField} required className="form-control" placeholder="phone" />
                                </div>
                            </div>

                            <div className="agreement mt-4 mb-4 text-center">
                                <span style={{fontSize:11}}>
                                    By submitting this form, I agree to StudyMate Privacy Policy
                                    and User Agreement.
                                    </span>
                            </div>
                            <button type="submit" className="register">Sign in</button>
                        </form>
                    </div>
                </div>
            </div>

        </div>

    )
}

export default RegisterForm;
