import React from 'react'

export default function Searchbar() {
    return (
        <ul> 
            <li>
                <div className="h-100 nav-search" >
                    <div className="d-flex justify-content-center h-100">
                        <div className="searchbar">
                        <input className="search_input" type="text" name="" placeholder="Type here to search"/>
                        <a className="search_icon"><img className="search-width" src="images/search.png"/></a>
                        </div>
                    </div>
                </div>
            </li>   
        </ul>
    )
}
