const studies = quizData;
const videos = VideoData;

const Wrapper = ({ isBusy, setCourses, setVideos, ...props }) => {
  useEffect(() => {
    isBusy(true);
    setCourses(studies);
    setVideos(videos);
    isBusy(false);
    document.body.classList.add("no-overflow");
  }, [isBusy, setCourses, setVideos]);

  const isQuizPage = props.history.path.toLowerCase().includes("quiz") && props.history.id;

  const modeFromStorage = ls.getItemInLs("mode");
  !props.mode && props.setMode(modeFromStorage);
  const getMode = () => props.mode || modeFromStorage;

  const isHideablePage = () => isQuizPage && getMode();

  return (
    <div>
      <div className="container-fluid p-0">
        <StudyNav
          setHistory={props.setHistory}
          isHiddenMode={!isHideablePage()}
        >
          <StudyNavRight type={props.navType} />
        </StudyNav>
      </div>

      <div className="row d-inline m-0">
        {!isHideablePage() && (
          <div className="d-inline">
            <ConnectedSideNav />
          </div>
        )}

        <div className="d-inline">{props.children}</div>
      </div>
    </div>
  );
};

export default Wrapper;
