import React from 'react'

const StudyNavBar = (props) => {
    //props.children is populated in that space
    return (
        <div className="fixed-top never-move">
            <header id="header">
                <div className="container">
                    <div className="logo float-left nav-padding ">
                        <a className="scrollto"><img src="images/logo.png" alt="" className="img-fluid" /></a>
                </div>
  
                <nav className="main-nav float-right d-none d-lg-block">
                    <ul>
                        {props.children}
                    </ul>
                </nav>
                </div>
            </header>
  
       </div>
    );
}

export default StudyNavBar;