import React from 'react'
import SecondLevelContent from './SecondLevelContent';

const SecondLevel = ({juniorSubjects, setHistory}) => {
    return (
        <div
            style={{
                position: "relative",
                width: "100%",
                borderRadius: "0px"
            }}
            >
            {juniorSubjects && juniorSubjects.Video.map((subject, key) => {
                return (
                    <SecondLevelContent key={key} setHistory={setHistory} subjects={subject}/>
                )
            })}
            </div>
    )
}

export default SecondLevel;