import React, {useState, useEffect} from "react";
import SideMenu from "./SideMenu";
import Overlay from "./Overlay";
import Decision from "./Decision";
import ParserCondition from "./ParserCondition";
import track from '../../sideEffects/apis/courseTracker'
import ls from '../../sideEffects/ourLocalStorage'


//const asciimath = '`sum_(i=1)^n i^3=((n(n+1))/2)^2`'
//const content = `This can be dynamic text (e.g. user-entered) text with ascii math embedded in  symbols like $$ ${ascii}$$`

const UpsertCourse = (courses, course) => {
  console.log(courses, course)
  return courses.find(c => c.courseId === course.courseId) ? 
      courses.map(c => c.courseId === course.courseId ? course : c) : courses.concat(course)
}


const Quiz = ({ isBusy, id, addCourse, courses, ...props }) => {
  console.log('this is the quiz id', id)
  console.log(props.activeCourse)
  console.log('props.activeCourse')
  
    props.type(props.mode);
    const questionId = Number(props.history.questionId);
    console.log('ques id', questionId)
    
    
    const [disable, showDisable] = useState(false)
    const [changeTracker, setChangeTracker] = useState(false)
    const mainQuiz = courses ? courses.quizes.find(course => course.id === questionId) : null;
    const thisQuestionInStore = props.activeCourse ? props.activeCourse.question.find(q => q.questionId === questionId ) : null;
    
    function pushToUser() {
      const activeC = ls.getItemInLs('activeCourse');
      
      const editedUserDetails = {...props.user, courses: UpsertCourse(props.user.courses, activeC)}
      props.setActiveUser(editedUserDetails);
      props.editUser(editedUserDetails);
  }

    const finalizeTracker = (qId) => {
      sendQuestionToStore();
      props.setHistory({ ...props.history, questionId: qId });
      setChangeTracker(!changeTracker);
    }
    console.log('q id', questionId)
    
    console.log('this question',thisQuestionInStore)
    //main option
    
    const [mainOption, setMainOption] = useState()
    useEffect(() => {
      setMainOption(thisQuestionInStore ? thisQuestionInStore.userAnswerId : 0)
      // return () => {
      //   const userFromStorage = ls.getItemInLs('user');
      //   const editedUserDetails = {...userFromStorage, courses: UpsertCourse(props.user.courses, props.activeCourse)}
      //   props.setActiveUser(editedUserDetails);
      //   props.editUser(editedUserDetails);
        
      // }
      
    }, [changeTracker])

    const [generatedQuestion, setGeneratedQuestion] = useState()
    

  const onOptionChange = (id) => {
    setGeneratedQuestion(track.questionBuilder(mainQuiz.id, id, mainQuiz.answerId));
    setMainOption(id);
  }
  props.type(props.mode);
  const incrementQuestion = () => {
    console.log(questionId, courses.quizes.length)
      if (questionId < courses.quizes.length) {
        finalizeTracker(questionId + 1);
    }
  }
   const previousQuestion = () => {
       if (questionId > 0) {
        finalizeTracker(questionId - 1);
     }
    }
    function sendQuestionToStore() {
      let g = generatedQuestion;
      if(!g){
        console.log('inside g', g)
        if(!thisQuestionInStore) return
        g = track.questionBuilder(thisQuestionInStore.questionId, thisQuestionInStore.userAnswerId, thisQuestionInStore.correctAnswerId)
        console.log('inside g', g)
      }
      const score = ls.getItemInLs('activeCourse').question.reduce((acc, cur) => track.correctAnswer(cur.userAnswerId, cur.correctAnswerId) ? acc + 1 : acc, 0)
      props.updateScore(score)
      props.updateActiveCourse(g);
      pushToUser()
    }
    console.log(generatedQuestion)
    console.log(props.activeCourse)
    console.log('main quiz', mainQuiz)

    

    const disabledButtons = (val) => {
      showDisable(val)
    }
   console.log(disable)
  return (
    <React.Fragment>
      <div className="container">
        {mainQuiz && (
          <div className="row m-0">
            <div className="col-12  padding-left0">
              <div className="quizQuestion">
                  <ParserCondition question={mainQuiz.question} isMathjax={mainQuiz.isQuestionMathjax}/>
              </div>
            </div>
            <div className="col-12 padding-left0">
              <div className="optionSection">
                <form>
                  {mainQuiz.options.map(option => (
                    <label className="contain" key={option.id}>
                      <span className="question">
                      <ParserCondition question={option.content} isMathjax={option.isMathjax}/>
                      </span>
                      <input 
                        onChange={() => onOptionChange(option.id)} 
                        checked={mainOption === option.id} 
                        type="radio" name="radio" />
                      <span className="checkmark"></span>
                  </label>
                  ))}
                  
                </form>
              </div>
            </div>
            <div className="d-inline quiz-side"></div>
            
          
            <SideMenu questions={courses.quizes.length} questionId={questionId} finalizeTracker={(qId) => finalizeTracker(qId)} history={props.history} setHistory={props.setHistory} disable={disabledButtons}/>
            <>
            {props.mode !== "LEARN_MODE" ? <> 
               <button className="previous-button" style={{display: questionId === 1 ? 'none' : 'inline'}} onClick={previousQuestion}>PREVIOUS</button>
               {questionId === courses.quizes.length ?  
                    <Overlay mode={props.mode} user={props.user} editUser={props.editUser}  
                      setActiveUser={props.setActiveUser} 
                      activeCourse={props.activeCourse}
                      className="next-button"
                      totalQuestions={courses.quizes.length}
                      sendQuestionToStore={sendQuestionToStore}
                      setHistory={props.setHistory} /> : <button className="next-button" onClick={incrementQuestion}>NEXT QUESTION</button>}
               
              
            </> :
            <Decision disable={disabledButtons} 
                      explanation={mainQuiz.answerUrl} setHistory={props.setHistory} 
                      finalizeTracker={(qId) => finalizeTracker(qId)}
                      isLast={questionId === courses.quizes.length}
                      id={id} questionId={questionId}/>
            
            }
            
           
          </>
          </div>
          
        )}
      </div>
    </React.Fragment>
  );
};

export default Quiz;


