import ls from '../sideEffects/ourLocalStorage'
const historyReducer = (state = null, action) => {
    switch (action.type) {
        case "CHANGE_HISTORY":
            ls.persistItemInLS("history", action.data, 3)
            console.log("This is the state", state)
            return action.data
        default:
            return state;
    }
}

export default historyReducer;