import React from 'react'
import track from '../../sideEffects/apis/courseTracker'

const SkillAssessment = (props) => {
    const onClick = () =>{
        props.setMode("SKILL_MODE");
        props.timeValue(1200);
        props.setHistory({path:"quiz", id:1, questionId: 1})
        props.setActiveCourse(track.courseBuilder(props.history.mainId, props.history.id, "SKILL_MODE"));
    }
    return (
     <div>
      <div className="skill-page">
          <div className="container">
          <div className="row text-center">
              <div className="card skill-padding">
        <div className="card-body">
        <div className="col-md-6">
           <img className="img-skill" src="images/picture.png"/>
        </div>
        <div className="col-md-6">
            <p className="card-text text-skill ">Please start by taking a preparatory quiz. We will use this to improve our questioning. </p>
             <button onClick={onClick} className="start-button">START NOW</button>
        </div>
       
        </div>
              </div>
    
        </div>
        </div>
   </div>
 </div>
    )
}

export default SkillAssessment;