import React from "react";

const VideoList = ({ list, setHistory, contentz }) => {
  const videoItem = list.map(item => {
    return (
      <div
        className="video-item row"
        onClick={() =>
          setHistory({
            path: "video",
            id: item.id,
            contents: contentz
          })
        }
      >
        <div className="col-5 padding-left0 padding-right0">
          <img
            src="images/singleVid.png"
            className="img-responsive"
            alt="general Thumbnail"
          />
        </div>

        <div className="col-7">
          <h5>{item.topic}</h5>
          <div className="short-desc">{item.videoName}</div>
          <div className="ratings"></div>
        </div>
      </div>
    );
  });
  return <div>{videoItem}</div>;
};

export default VideoList;
