import React from 'react'
import Searchbar from '../ToRightOfNav/Searchbar/Searchbar'
import NoTimeNav from '../ToRightOfNav/NoTimeNav/NoTimeNav'
import ConnectedTimeNav from '../ToRightOfNav/TimeNav/ConnectedTimeNav'

export default function StudyNavRight({type}) {
    return (
        <React.Fragment>
            {type === "SEARCH_BAR" && <Searchbar />}
            {type === "LEARN_MODE" && <NoTimeNav text="Learning Approach" />}
            {(type === "TIME_MODE" || type === "SKILL_MODE") && <ConnectedTimeNav />}
            {type === "FREE_FORM" && <NoTimeNav text="Free form mode"/>}
        </React.Fragment>
    )
}








    