import React from 'react';
import ConnectedHamburger from '../Hamburger/connectedHamburger';
//import Logout from '../Authentication/Logout';
//import { logout } from '../../sideEfects/apis/auth';

const StudyNav = (props) => {
    return (
        <header id="header">
            {props.isHiddenMode && <ConnectedHamburger />}
            <div className="container">
                <div className="logo float-left nav-padding ">
                    <a onClick={() => props.setHistory({ path: "home" })} className="scrollto c-pointer"><img src="images/logo2.svg" alt="app logo identity" className="img-fluid" /></a>
                </div>
                <nav className="main-nav float-right d-none d-lg-block">
                    {props.children}
                </nav>

            </div>
        </header>

    );
}

export default StudyNav;