import React from "react";
import ConnectedWrapper from "../components/Wrapper/ConnectedWrapper";
import ConnectedCoursesObj from "../components/Quiz/ConnectedCoursesObj";
import ConnectedQuiz from "../components/QuizProper/ConnectedQuiz";
import ConnectedVideo from "../components/Video/ConnectedVideo";
import ConnectedHome from "../components/Home/ConnectedHome";

const AppPresentation = props => {
  console.log("history in ap", props.history);
  return (
    <React.Fragment>
      {props.history.path === "home" && <ConnectedHome type={props.setNav} />}
      {props.history.path === "courses" && (
        <ConnectedWrapper>
          <ConnectedCoursesObj type={props.setNav} id={props.history.id} />
        </ConnectedWrapper>
      )}
      {props.history.path === "quiz" && (
        <ConnectedWrapper>
          <ConnectedQuiz
            id={props.history.id}
            questionId={props.history.questionId}
            type={props.setNav}
          />
        </ConnectedWrapper>
      )}
      {props.history.path === "video" && (
        <ConnectedWrapper>
          <ConnectedVideo
            id={props.history.id}
            type={props.setNav}
            contents={props.history.contents}
          />
        </ConnectedWrapper>
      )}
    </React.Fragment>
  );
};

export default AppPresentation;
