// JavaScript source code
const startNewCourse = (userId) => {
    return {
        userId,
        courses: [],
    }
}

const correctAnswer = (userAnswerId, correctAnswerId) => userAnswerId === correctAnswerId;
const courseBuilder = (mainId, courseId, mode) => ({mainId, courseId, mode, question: [], isComplete: false})
const questionBuilder = (questionId, userAnswerId, correctAnswerId) => ({questionId, userAnswerId, correctAnswerId})

export default {
    startNewCourse,
    correctAnswer,
    courseBuilder,
    questionBuilder
};