import React from 'react';
import StudyNavBar from '../StudyNavBar/StudyNavBar';

const AuthNavigation = (props) => {
    return (
        <React.Fragment>
            {!props.user &&
                <StudyNavBar>
                    {props.users.length < 5 &&
                        <React.Fragment>
                            <li className="nav-padding "> <a>{props.text}? </a></li>
                            <li className="nav-padding ">
                                <a className="blue-ish c-pointer" onClick={() => props.setHistory(props.path)}> {props.link}</a>
                            </li>
                        </React.Fragment>
                    }
                    
                </StudyNavBar>
            }
        </React.Fragment>


    )
}


export default AuthNavigation;