import React from 'react'

const Avatar = ({ name, sex, id, submitForm }) => {
    return (
            <div className="text-center c-pointer">
                <p className="name">{name}</p>
                <img  onClick={(e) => submitForm(e, id)} src={sex === 'Male' ? 'images/male.png' : 'images/female.png'} alt={`${name}'s avatar icon`} className="img-fluid c-pointer" />
            </div>
      

    )
}

export default Avatar;